export default {
    data() {
        return {
            agree: true,
            aplay: true,
            usdt: true,
            pool: ''
        };
    },
    onLoad(option) {
        this.pool = JSON.parse(option.a);
    },
    methods: {
        pay() {
            this.aplay = !this.aplay;
        },
        usd() {
            this.usdt = !this.usdt;
        },
        agreement() {
            this.agree = !this.agree;
        }
    }
};
